import Vue from 'vue'
import Cart from './cart/Cart.vue'
import CategoryButton from './categoryButton/CategoryButton.vue'
import Location from './location/Location.vue'
import store from './store'

Vue.config.productionTip = false

new Vue({
  store,
  components: {
    'location-modal': Location
  }
}).$mount('location-modal')

new Vue({
  store,
  components: {
    'product-cart': Cart
  }
}).$mount('product-cart')

const buttons = [...document.querySelectorAll('.category-button, .product-form__to-cart')]

buttons.forEach(element => {
  new Vue({
    store,
    render: h => h(CategoryButton, {
      props: {
        dataArray: element.dataset.add2basket.split(',')
      }
    })
  }).$mount(`#${element.id}`)
})

