import axios from 'axios'

const url = window.spa_url

export default {
  async getCart (callback) {
    try {
      const response = await axios.get(`${url}/cart`)
      callback(response.data.cart)
    } catch (error) {
      console.log(error)
    }
  },
  async addToCart (callback, data) {
    try {
      const [product, price, city] = data
      const response = await axios.post(`${url}/cart/add`, {
        product_id: product,
        price_id: price,
        city_id: city
      })
      callback(response.data.cart)
    } catch (error) {
      console.log(error)
    }
  }
}
