window.addEventListener('load', () => {
  const seoBlock = [...document.querySelectorAll('.seo-block')]
  const getValue = (el, property) => {
    return parseInt(window.getComputedStyle(el).getPropertyValue(property))
  }

  seoBlock.forEach(element => {
    const list = element.querySelector('.seo-block__list')
    const marginBottom = getValue(list, 'margin-top')
    const marginTop = getValue(list, 'margin-bottom')
    const height = marginBottom + marginTop + list.offsetHeight

    element.addEventListener('click', (e) => {
      e.preventDefault()
      const target = e.target

      if (target.classList.contains('seo-block__expand')) {
        element.style.height = `${height}px`
        target.style.display = 'none'
      }
    })
  })
})
