import cart from '../../api/cart'

const state = {
  cart: {}
}

const getters = {
  getCount: (state) => {
    let count = 0
    let quantityArray

    if (state.cart.items) {
      quantityArray = state.cart.items.map(item => item.quantity)
      count = quantityArray.reduce((accumulator, currentValue) => accumulator + currentValue)
    }

    return count
  }
}

const mutations = {
  setcart (state, payload) {
    state.cart = payload
  }
}

const actions = {
  setCart ({ commit }) {
    cart.getCart(cart => {
      commit('setcart', cart)
    })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}
