document.addEventListener('DOMContentLoaded', () => {
  const phoneCode = [...document.querySelectorAll('.phone-code')]

  phoneCode.forEach(element => {
    // const input = element.parentNode.querySelector('input')
    const list = element.querySelector('ul')
    const field = element.querySelector('.phone-code__field')
    const chevron = element.querySelector('i')
    const active = list.firstElementChild
    const flag = active.firstElementChild.cloneNode()
    const items = [...list.children]
    element.dataset.phoneCode = active.textContent.trim()
    field.insertBefore(flag, chevron)
    items.forEach(item => {
      item.addEventListener('click', (e) => {
        e.preventDefault()
        const target = e.target
        const check = target.classList.contains('phone-code__item')
        let flag

        if (check) {
          flag = target.firstElementChild.cloneNode()
          element.dataset.phoneCode = target.textContent.trim()
          field.replaceChild(flag, field.firstElementChild)
          list.classList.remove('phone-code__list_open')
        }
      })
    })
    field.addEventListener('click', (e) => {
      list.classList.toggle('phone-code__list_open')
    })
    /* input.addEventListener('input', (e) => {
      const target = e.target
    }) */
  })
})
