<template>
  <button
    @click.prevent="addCart(dataArray)"
    class="category-list__item-button"
  >
    в корзину
    <slot />
  </button>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'CategoryButton',
  props: {
    dataArray: {
      type: Array,
      required: true,
      default: function () {
        return []
      }
    }
  },
  methods: {
    ...mapActions(['addCart'])
  }
}
</script>

<style lang="scss">
  .category-list__item-button {
    padding: 8.5px 18.5px;
    border: 1px solid #8fb35b;
    background: none;
    border-radius: 3px;
    color: #515151;
    font-size: 14px;
    cursor: pointer;

    &:hover {
      color: #fff;
      background-color: #8fb35b;
    }

    @media (max-width: 767px) {
      padding: 5.5px 18.5px;
      margin: auto;
      font-size: 11px;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      padding: 6px 17px;
      flex-shrink: 0;
    }
  }
</style>
